// "use client"
import Image from 'next/image'
import React, { useEffect, useState } from 'react'

const Recipes = () => {

    const [recipes, setRecipes] = useState([]);
    const [loading, setLoading] = useState<boolean>(true);


         useEffect(() => {
        const fetchRecipes = async () => {
          try {
            const response = await fetch('www.themealdb.com/api/json/v1/1/search.php?s=Chicken', {
              method: 'GET',
            //   headers: {
            //     'Content-Type': 'application/json',
            //     Accept: 'application/json',
            //   },
            });
            const data = await response.json();
                    console.log("Data",data)
    
            setRecipes(data?.categories);
          } catch (error) {
            console.error('Error fetching recipes:', error);
          } finally {
            setLoading(false);
          }
        };
    
        fetchRecipes();
      }, []);

        if (loading) return <p>Loading...</p>;


  return (
    <div className='flex flex-col justify-center w-full items-center mb-10'>
       
            <div className='flex flex-wrap justify-center w-full items-center'>
                {
                    recipes.map((item: any) => {

                        return (
                            <div key={item.idMeal} className='group w-100 h-30 text-black hover:bg-[#FF7518] hover:text-white m-3 flex flex-row justify-center items-center rounded-[.5vw] cursor-pointer'>

                                <div className='w-25'>
                                    <Image
                                        src={item?.strMealThumb}
                                        alt="Food Item"
                                        width={100}
                                        height={100}
                                        className="object-cover" />
                                </div>
                                <div className='w-75 flex flex-col ml-1'>
                                    <span className=' font-bold font text-[16px] group-hover:text-white'>{item?.strMeal}</span>
                                    <span className='text-[#A9A9A9] font text-[12px] group-hover:text-white'>
                                        {item?.strInstructions
                                            ?.split(' ')                            
                                            .slice(0, 20)                         
                                            .join(' ') + '...'}
                                        </span>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
           
        </div>
  )
}

export default Recipes